import 'react-datepicker/dist/react-datepicker.css'

import React from 'react'
import DatePicker, { ReactDatePickerProps } from 'react-datepicker'
import { Field, FieldType } from 'react-form'
import styled from 'styled-components'

import moment from '../helpers/moment'

interface IProps
  extends Omit<ReactDatePickerProps, 'onChange' | 'selected' | 'minDate'> {
  field: FieldType
  defaultValue?: Date
  inputWidth?: string
  minDate?: Date
}

const FormInputDateContainer = styled.div`
  & .react-datepicker-wrapper,
  .react-datepicker__input-container,
  .react-datepicker__input-container input {
    width: ${({ width = '6rem' }: { width?: string }) => width};
  }
`
FormInputDateContainer.displayName = 'FormInputDateContainer'

export const FormInputDate: React.SFC<IProps> = ({
  field,
  defaultValue,
  inputWidth,
  minDate,
  ...props
}) => (
  <FormInputDateContainer width={inputWidth}>
    <Field
      field={field}
      defaultValue={defaultValue}
      render={fieldApi => (
        <DatePicker
          selected={moment(fieldApi.value || new Date())}
          onChange={date => fieldApi.setValue(date)}
          minDate={minDate === undefined ? undefined : moment(minDate)}
          {...props}
        />
      )}
    />
  </FormInputDateContainer>
)
