import React from 'react'

import { GCompanyBranch } from '../graph/types'
import { RM } from '../helpers/ramda'
import {
  CompanyBranchDropdown,
  ICompanyBranchDropdownProps,
} from '../molecules/CompanyBranchDropdown'
import { CompanyBranchFlatSelect } from '../molecules/CompanyBranchFlatSelect'
import { UserCompanyBranches } from '../organisms/UserCompanyBranches'

type TBranch = GCompanyBranch.Fragment

interface ICompanyBranchPickerProps {
  selectedBranches?: ID[]
  onSelectionChange?(branchIds: ID[], branches: TBranch[]): void
  onSelectionClear?(): void
  breakpoint?: number
  isMulti?: boolean
  isClearable?: boolean
  textColor?: string
  getDropdownProps?(): Partial<ICompanyBranchDropdownProps>
}

export interface ICompanyBranchPickerImpl {
  branches: TBranch[]
  isMulti?: boolean
  isClearable?: boolean
  isLoading?: boolean
  selectedBranch?: Maybe<TBranch>
  onSingleSelection?(branch: Maybe<TBranch>): void
  selectedBranches?: TBranch[]
  onMultiSelection?(branches: TBranch[]): void
  textColor?: string
}

export const CompanyBranchPicker: React.SFC<ICompanyBranchPickerProps> = ({
  breakpoint = 3,
  selectedBranches = [],
  isMulti = false,
  isClearable = true,
  textColor,
  onSelectionChange,
  onSelectionClear,
  getDropdownProps = () => ({}),
}) => (
  <UserCompanyBranches
    render={(branches, isLoading) => {
      const isSelected = branch => selectedBranches.includes(branch.id)
      const props: ICompanyBranchPickerImpl = {
        textColor,
        isClearable,
        isMulti,
        isLoading,
        branches,
        selectedBranch: isLoading ? null : branches.find(isSelected),
        selectedBranches: isLoading ? [] : branches.filter(isSelected),
        onSingleSelection(selected: TBranch) {
          if (selected === null && onSelectionClear) {
            onSelectionClear()
          } else if (selected !== null && onSelectionChange) {
            onSelectionChange([selected.id], [selected])
          }
        },
        onMultiSelection(selected: TBranch[]) {
          if (selected.length === 0 && onSelectionClear) {
            onSelectionClear()
          } else if (selected.length && onSelectionChange) {
            onSelectionChange(selected.map(RM.prop('id')), selected)
          }
        },
      }
      if (branches.length < breakpoint) {
        return <CompanyBranchFlatSelect {...props} />
      }
      return <CompanyBranchDropdown {...props} {...getDropdownProps()} />
    }}
  />
)
