import 'react-datepicker/dist/react-datepicker.css'

import React from 'react'
import DatePicker from 'react-datepicker'
import { Field, FieldType } from 'react-form'
import styled from 'styled-components'

import moment from '../helpers/moment'

interface IFormInputTimeProps {
  field: FieldType
  timeIntervals?: number
  timeFormat?: string
}

const FormInputTypeContainer = styled.div`
  display: inline-block;
  & .react-datepicker__input-container,
  .react-datepicker__input-container input {
    width: inherit;
  }
  & .react-datepicker-wrapper {
    width: 3rem;
  }
  &
    .react-datepicker__time-container
    .react-datepicker__time
    .react-datepicker__time-box
    ul.react-datepicker__time-list {
    padding-left: unset;
    padding-right: unset;
    width: 100px;
  }
`
FormInputTypeContainer.displayName = 'FormInputTypeContainer'

export const FormInputTime: React.SFC<IFormInputTimeProps> = ({
  field,
  timeIntervals = 30,
  timeFormat = 'LT',
}) => (
  <FormInputTypeContainer>
    <Field
      field={field}
      render={fieldApi => (
        <DatePicker
          selected={moment(fieldApi.value || new Date())}
          onChange={time => fieldApi.setValue(time)}
          showTimeSelect={true}
          showTimeSelectOnly={true}
          timeIntervals={timeIntervals}
          dateFormat={timeFormat}
          timeFormat={timeFormat}
        />
      )}
    />
  </FormInputTypeContainer>
)
