import { observer } from 'mobx-react-lite'

import { useRoot } from '../atoms/RootProvider'
import { GCompanyBranch } from '../graph/types'

interface IProps {
  render(branches: GCompanyBranch.Fragment[], isLoading: boolean): ReactNode
}

export const UserCompanyBranches = observer<IProps>(({ render }) => {
  const { user } = useRoot()
  return user.branches ? render(user.branches, false) : render([], true)
})
