import { Trans } from '@lingui/react'
import React from 'react'
import { FieldType } from 'react-form'
import styled from 'styled-components'

import { FormInputTime } from './FormInputTime'

interface IFormInputTimeSpanProps {
  fieldFrom?: FieldType
  fieldTo?: FieldType
}

const LabelStyled = styled.div`
  margin: 0.5rem;
  display: inline-block;
`

const LabelToStyled = styled(LabelStyled)`
  margin-left: 2rem;
`

export const FormInputTimeSpan: React.SFC<IFormInputTimeSpanProps> = ({
  fieldFrom,
  fieldTo,
}) => (
  <>
    {fieldFrom && (
      <>
        <LabelStyled>
          <Trans id="input.timespan.from">from</Trans>
        </LabelStyled>
        <FormInputTime field={fieldFrom} />
      </>
    )}
    {fieldTo && (
      <>
        <LabelToStyled>
          <Trans id="input.timespan.to">to</Trans>
        </LabelToStyled>
        <FormInputTime field={fieldTo} />
      </>
    )}
  </>
)
